body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Archivo', sans-serif;
}

.landing-page-left-centered h1 {
    font-weight: 600; 
    font-size: 59px;
}

.landing-page {
    display: flex;
    position: relative;
    height: 100vh;
    width: 100vw;
}

.mobile-hero-image {
    display: none;
}

.landing-page-left {
    display: flex;
    flex: 40% 1;
    background-color: rgb(249, 246, 241);
    flex-direction: column;
}

.landing-yelp-camp-icon {
    width: 134px;
    padding-left: 12vw; /* matches with 1 */
    padding-top: 33px;
}

.landing-page-left-centered {
    padding-left: 12vw; /* matches with 1 */
    padding-right: 12vw;
    margin-top: auto;
    margin-bottom: auto;
    text-align: left; 
}

.landing-page-left-centered p {
    margin-top: 1vw;
    font-size: 20px;
    line-height: 33px;
    color: rgb(102, 99, 94);
}

.landing-page-left-centered button {
    border: none;
    outline: 0.1vw solid black;
    background: black;
    color: white;
    font-weight: 600;
    font-size: 20px;
    padding: 21px;
    border-radius: 0.25vw;
    margin-top: 17px;
    margin-bottom: 17px;
    cursor: pointer;
}

.landing-page-left-centered button:hover {
    background: white;
    color: black;
    transition: all 0.3s ease 0s;
}

.landing-page-list {
    list-style: none;
    display: flex;
    flex-direction: column;
    grid-gap: 17px;
    gap: 17px;
    padding-top: 17px;
}

.landing-page-list li {
    display: flex;
    flex-direction: row;
    font-size: 20px;
    color: rgb(102, 99, 94)
}

.landing-page-list li:before {
    content: '';
    width: 25px;
    height: 25px;
    background-image: url(/yelp-camp/static/media/Checkmark.51d80222.svg);
    background-size: cover;
    background-position: center;
    display: block;
    margin-right: 1vw;
}

.sponsor-gallery {
    display: flex;
    flex-direction: row;
    grid-gap: 17px;
    gap: 17px;
}

.sponsor-gallery img {
    width: 117px;
    padding: 0;
}

@media screen and (max-width: 1230px){
    .landing-page {
        display: flex;
        position: relative;
        height: 100vh;
        width: 100vw;
        flex-direction: column; 
    }
 
    .landing-page-left {
        display: flex;
        background-color: rgb(249, 246, 241);
        flex-direction: column;
        flex: 1 1 40%;
    }

    .desktop-hero-image {
        display: none;
    }

    .mobile-hero-image {
        display: flex;
        width: 100%;
        height: 50vw;
        object-fit: cover;
        margin: 33px 0vw 33px 0vw;
    }

    .landing-page-left-centered {
        padding-left: 5vw; 
        padding-right: 5vw;
        margin-top: auto;
        margin-bottom: auto;
        text-align: left; 
    }
}

